.cardss {
    background-color: #252836;
    border-radius: 30px;
    margin: 50px 0;
}
h6 {
    font-size: 14px !important;
}
img {
    width: 100%;
    border-radius: 30px;
}
.text {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 20px 15px;
}
.send {
    margin-left: 8px;
}
.send, .heart {
    font-size: 1.2rem;
    border-radius: 100%;
}
.card-image {
    background-image: url("../images/image1.png");
    background-size: 100% 100%;
    height: 258px;
    background-repeat: no-repeat;
    border-radius: 30px;
}
.card-image img {
    height: 100%;
    width: unset !important;
}
h6 {
    font-size: 18px !important;
    font-weight: 600 !important;
    line-height: 18px;
}
.top {
    font-size: 14px;
    font-weight: 500;
    line-height: 1.7rem !important;
}
.mid {
    font-size: 12px;
    font-weight: 400;
    color: #808191;
}
.bot {
    font-size: 11px;
    font-weight: 400;
    color: #808191;
}
.edit {
    display: flex;
    flex-wrap: nowrap;
    overflow-x: auto;
}
.edit::-webkit-scrollbar {
   display: none;
}
.with {min-width: 80vw}
.ml, .mr-1 {margin-right: 2em !important;}

article li{
    width: 60px !important;
    height: 60px !important;
    
    /* border-image: linear-gradient(180deg, #1C85A6 0%, #6C5ECF 100%) !important; */
}
article li:first-child, article li:nth-child(2){
    margin-right: 1em;
    border-radius: 100%;
    border-style: solid;
    border-width: 3px;
    border-color: #1C85A6;
}
article li:nth-child(1)::after, article li:nth-child(2)::after  {
    content: '';
    position: absolute;
    background-image: url('../images/tick.png');
    width: 24px;
    height: 24px;
    top: 103px;
    /* right: 0px; */
    transform: translateX(23px);
}
li img {
    width: 100%;
    padding: 4px;
}